
.slider {
width: 424px;
}

.slide-window {
position: relative;
overflow: hidden;
margin-bottom: 24px;

}

.slide-window:hover .slider-arrow {
display: flex;
}

.slides { 
display: flex;
width: 500%;
transition: 0.5s all;
}

img {
width: 100%;
}

.slides-box {
display: flex;
justify-content: space-between;

}

.stuff-item {
cursor: pointer;
width: 88px;
height: 110px; 
}

.slider-arrow {

display: none;
top: 50%;
position: absolute;
width: 100%;

justify-content: space-between;
}

.slider-arrow__item {
/* display: none; */
width: 20px;
height: 20px;
}

.arrow{
width: 100%;
cursor: pointer;
}

.slider-arrow__item-left {
    rotate: 180deg;
}